  height: 0;\n  }\n}\n\n@mixin insertImageBefore($width, $height, $url) {\n  &::before {\n    display: inline-block;\n    content: \"\";\n    width: $width;\n    height: $height;\n    background-repeat: no-repeat;\n    background-size: contain;\n    background-image: $url;\n    @content;\n  }\n}","body {\n  display: grid;\n  width: 100vw;\n  height: 100vh;\n\n  $header-height: 45px;\n\n  // 標準では2カラム\n  grid-template-columns: ($screen-width-medium * 0.3) 1fr;\n  grid-template-rows: $header-height 1fr;\n\n  @media screen and (min-width: $screen-width-large) {\n    grid-template-columns: ($screen-width-large * 0.3) 1fr;\n    grid-template-rows: $header-height 1fr;\n  }\n\n  > header {\n    grid-column: 1 / 3;\n    grid-row: 1 / 2;\n  }\n  > div.menu {\n    grid-column: 1 / 2;\n    grid-row: 2 / 3;\n    overflow-y: scroll;\n    @include scrollbar;\n  }\n  > main {\n    grid-column: 2 / 3;\n    grid-row: 2 / 3;\n    overflow-y: scroll;\n    @include scrollbar;\n  }\n\n  // 幅がscreen-width-medium以下のデバイスでは1カラム\n  @media screen and (max-width: $screen-width-medium) {\n    display: block;\n\n    > header {\n      grid-row: 1 / 2;\n    }\n    > div.menu {\n      display: none;\n      position: absolute;\n      top: $header-height;\n      left: 0;\n      overflow-y: unset;\n      @include deleteScrollBar;\n\n      &.shown {\n        display: block;\n      }\n    }\n    > main {\n      grid-row: 2 / 3;\n      overflow-y: unset;\n    }\n  }\n}","body > header {\n  background-color: $c-bg-header;\n  color: $c-text;\n  display: flex;\n  align-items: center;\n  overflow-x: hidden;\n\n  div.menu-button {\n    display: none;\n    @media screen and (max-width: $screen-width-medium) {\n      display: flex;\n    }\n\n    width: 2em;\n    text-align: center;\n    height: 100%;\n    align-items: center;\n    justify-content: center;\n\n    &:hover {\n      background-color: $c-bg-header-hover;\n    }\n    &:active {\n      background-color: $c-bg-header-active;\n    }\n  }\n\n  div.title {\n    font-family: 'Arvo', 'Zen Kaku Gothic New', sans-serif;\n    color: $c-def-highlight3;\n    font-size: 1.5em;\n    margin-left: 0.5em;\n\n    > a {\n      color: inherit;\n      text-decoration: none;\n    }\n\n    @media screen and (max-width: $screen-width-medium) {\n      display: none;\n    }\n  }\n\n  div.search {\n    flex-grow: 1;\n\n    form {\n      display: flex;\n      align-items: center;\n      justify-content: right;\n      height: 100%;\n\n      input {\n        display: block;\n        border: 1px solid $c-def-white;\n        border-radius: $radius;\n        width: 1em;\n        max-width: 15em;\n        background-color: $c-bg-header;\n        color: $c-text;\n        padding: 0 0.5em;\n        font-size: 1.3em;\n        flex-grow: 1;\n\n        &:hover {\n          background-color: $c-bg-header-hover;\n        }\n        &:focus {\n          background-color: $c-text;\n          color: $c-bg-header;\n        }\n      }\n\n      button {\n        display: none;\n      }\n\n      label {\n        display: flex;\n        align-items: center;\n        padding: 0 0.25em;\n        height: 100%;\n        background-color: $c-bg-header;\n\n        &:hover {\n          background-color: $c-bg-header-hover;\n        }\n        &:active {\n          background-color: $c-bg-header-active;\n        }\n      }\n    }\n  }\n}","body > div.menu {\n  padding: 0.3em;\n  background-color: $c-bg;\n\n  @media screen and (max-width: $screen-width-medium) {\n    font-size: 1.25em;\n    line-height: 1.5em;\n  }\n\n  ul {\n    padding: 0 0 0 1em;\n\n    li {\n      list-style: none;\n      line-height: 1.25em;\n\n      @include insertImageBefore(0.8em, 0.8em, url('../icons/enabled.svg')) {\n        margin-right: 0.2em;\n      }\n    }\n  }\n\n  h2 {\n    border-left: 6px solid $c-def-white;\n    margin: 0.2em 0;\n    padding: 0.5em 0;\n    padding-left: 0.4em;\n  }\n\n  h3 {\n    margin: 0;\n    padding: 0.4em 0;\n  }\n\n  .collapsible-controller {\n    &:hover {\n      background-color: $c-bg-hover;\n    }\n    &:active {\n      background-color: $c-bg-active;\n    }\n\n    & + .collapsible {\n      display: none;\n    }\n    &.expanded + .collapsible {\n      display: inline-block;\n    }\n  }\n}","body > main {\n  padding: 0.4em 0.8em;\n\n  h1 {\n    margin: 1em 0;\n    line-height: 1.1em;\n  }\n\n  blockquote {\n    background-color: $c-bg-hover;\n    padding: 0.25em 1.25em;\n    margin: 0;\n    border-radius: $radius;\n  }\n\n  // 文中の `これ`\n  :not(pre) > code {\n    display: inline-block;\n    background-color: $c-bg-header;\n    border-radius: $radius;\n    font: inherit;\n    padding: 0 0.2em;\n  }\n\n  // 数式\n  code.math-inline {\n    background-color: transparent;\n  }\n  code.math-block {\n    display: block;\n    background-color: transparent;\n    width: 100%;\n    overflow-x: scroll;\n\n    @include scrollbar;\n  }\n\n  // スポイラー\n  .spoiler {\n    display: none;\n  }\n  .spoiler-controller.expanded + .spoiler {\n    display: initial;\n  }\n  .spoiler-controller {\n    height: 1.5em;\n    width: 100%;\n    background-color: $c-bg;\n\n    &:hover {\n      background-color: $c-bg-hover;\n    }\n    &:active {\n      background-color: $c-bg-active;\n    }\n  }\n\n  table {\n    border-collapse: collapse;\n\n    tbody {\n      tr {\n        &:nth-child(2n+1) {\n          background-color: $c-bg-hover;\n        }\n\n        td {\n          padding: 0.5em;\n\n          &:not(:first-child) {\n            border-left: 2px solid $c-text;\n          }\n        }\n\n        &:not(:first-child) td {\n          border-top: 2px solid $c-text;\n        }\n      }\n    }\n  }\n\n  // 真理値表\n  table.truth {\n    tbody {\n      tr {\n        td {\n          border-radius: $radius;\n          text-align: center;\n          border: none;\n        }\n\n        td:first-child {\n          background-color: #E49F44;\n        }\n        td.T {\n          background-color: #5F9F5F;\n        }\n        td.F {\n          background-color: #DF5F5F;\n        }\n        td._ {\n          background-color: #8F8F8F;\n        }\n      }\n    }\n  }\n\n  img {\n    max-width: 100%;\n  }\n\n  span.T, span.True {\n    color: $c-def-true;\n  }\n  span.F, span.False {\n    color: $c-def-false;\n  }\n}"],"sourceRoot":""}]);
